import React from "react";
import { Card, CardContent, Typography, Button } from "@mui/material";

const FlightCard = ({ flight }) => {
  return (
    <Card>
      <CardContent>
        <Typography variant="h6">{`${flight.airline} Flight`}</Typography>
        <Typography variant="body2">{`Price: $${flight.price}`}</Typography>
        <Typography variant="body2">{`Departure: ${flight.departure}`}</Typography>
        <Typography variant="body2">{`Arrival: ${flight.arrival}`}</Typography>
        <Button variant="contained" color="primary" fullWidth>
          Book Now
        </Button>
      </CardContent>
    </Card>
  );
};

export default FlightCard;
