import axios from "axios";

const API_URL = "https://sky-scrapper.p.rapidapi.com/flightsearch";
const API_KEY = "18becb2f2bmsh2a6d16062fbbda0p1df5f6jsna68e5294a9fa"; // Replace with your RapidAPI key

export const fetchFlights = async (origin, destination, departureDate, returnDate) => {
  try {
    const response = await axios.get(API_URL, {
      headers: {
        "X-RapidAPI-Key": API_KEY,
        "X-RapidAPI-Host": "sky-scrapper.p.rapidapi.com"
      },
      params: {
        origin,
        destination,
        departure_date: departureDate,
        return_date: returnDate
      }
    });
    return response.data;
  } catch (error) {
    console.error("Error fetching flight data:", error);
    return [];
  }
};
